import React from "react";
import { HashRouter, Router } from "react-router-dom"
import Home from "./routes/Home";
import About from "./routes/About";

function App() {
  return <HashRouter>
    <Router path="/">
      <h1>Home</h1>
      </Router>
    <Router path="/about">
      <h1>About</h1>
      </Router>
    </HashRouter>
}

export default App;


